/****************************************************************************************/
/* C9 Music Client */
/****************************************************************************************/
@import "_client_variables";

a:hover {
    color: $primary;
}

.entry-content p.has-huge-font-size,
.entry-content p.has-large-font-size,
.entry-content p.has-larger-font-size,
.entry-content p.has-medium-font-size,
.entry-content p.has-normal-font-size,
.entry-content p.has-regular-font-size,
.entry-content p.has-small-font-size {
    line-height: 1.6;
}

/****************************************************************************************/
/* C9 Music Nav header */
/****************************************************************************************/
.header-navbar {
    min-height: 45px;
}

.header-navbar .navbar {
    background-color: transparent;
}

.header-navbar .navbar-toggler,
.header-navbar .nav-search .btn-nav-search,
.header-navbar .nav-order .nav-shop-link {
    color: $light;
}

.header-navbar .navbar-toggler:hover,
.header-navbar .nav-search .btn-nav-search:hover,
.header-navbar .nav-search .btn-nav-search:hover .fa-search,
.header-navbar .navbar.navbar-light .nav-link:hover .fa,
.header-navbar .navbar-toggler:focus,
.header-navbar .nav-search .btn-nav-search:focus {
    color: $gray-500;
}

.header-navbar .navbar-small,
.blog .header-navbar,
.archive .header-navbar,
.single-post .header-navbar,
.error404 .header-navbar,
.search .header-navbar {
    background-color: $primary;
}

.navbar-small .header-navbar.jumpfix {
    min-height: 45px;
}

.header-navbar .justify-content-center {
    justify-content: flex-end !important;
}

.header-navbar .nav .search .nav-link .fa-search {
    font-size: 1.4rem;
}

.dropdown-toggle::after {
    border-right: none;
    border-left: none;
}

.wp-block-button__link.is-style-outline {
    color: inherit;
}

.header-navbar .navbar .nav .nav-item,
.header-navbar .navbar .nav .nav-item .dropdown-item,
.header-navbar .navbar .nav .nav-item .nav-link,
.header-navbar .navbar .nav .search,
.header-navbar .navbar .nav a::after {
    line-height: 1;
    text-shadow: 0.5px 0.866px 4px rgba(0, 0, 0, 0.92);
}

.header-navbar .navbar .nav .nav-item {
    margin: 0px 14px;
}

.header-navbar .navbar .nav .nav-item .dropdown-menu .nav-item {
    margin: 0px 6px;
}

.header-navbar .navbar .nav .search .nav-search a {
    font-size: 1.4rem;
}

.header-navbar .navbar .nav .nav-item .dropdown-item,
.header-navbar .navbar .nav .nav-item .nav-link {
    color: $light;
    font-size: 1.4rem;
    font-family: $font-family-sans-serif;
    text-transform: uppercase;
    position: relative;
    letter-spacing: 1px;
    padding: 14px 0px;
}

.header-navbar .nav-order a,
.header-navbar .navbar-small .nav-order a {
    text-decoration: none;
}

.header-navbar .nav-order a .count,
.header-navbar .navbar-small .nav-order a .count,
.header-navbar .navbar .nav .nav-item .nav-link .count {
    font-size: 1rem;
    margin-left: 2px;
}

.header-navbar .nav-item .nav-highlight {
    content: "";
    position: absolute;
    z-index: -1;
    left: 0;
    right: 100%;
    bottom: 0;
    background: $light;
    height: 4px;
    transition: right .3s;
    transition-timing-function: ease-out;
}

.header-navbar .nav-item>.nav-link:hover>.nav-highlight,
.header-navbar .nav-item>.nav-link:focus>.nav-highlight,
.header-navbar .nav-item:focus>.nav-link>.nav-highlight {
    right: 0% !important;
}

.header-navbar .navbar .nav .nav-item .dropdown-item {
    padding: 14px;
}

.header-navbar .navbar .nav .nav-item .dropdown-menu .nav-item .nav-link {
    padding: 14px 8px;
}

.header-navbar .nav .nav-item .dropdown-menu {
    background-color: $primary;
    border-radius: 0px;
    margin-top: 1em;
    border: 0px;
    -webkit-animation: fadeInFromNone 0.5s ease-out;
    animation: fadeInFromNone 0.5s ease-out;
}

.header-navbar .nav .nav-item .dropdown-menu.show {
    display: block;
    -webkit-animation: fadeInFromNone 0.5s ease-out;
    animation: fadeInFromNone 0.5s ease-out;
}

.header-navbar .nav .nav-item .dropdown-menu .dropdown-item:hover {
    background-color: $dark;
    color: $light;
}

.nav-search,
.nav-toggle,
.nav-order {
    display: inline-block;
    line-height: 2;
}

.header-navbar .nav-order .nav-shop-link {
    font-size: 2rem;
    margin: 0;
    line-height: 1;
    cursor: pointer;
}

.navbar-toggler[aria-expanded="true"] .fa-bars:before {
    content: '\f00d';
}

.nav-search {
    margin-right: 10px;
}

@media only screen and (max-width: 991px) {
    .header-navbar .nav .nav-item.nav-woocommerce {
        display: none;
    }
}

@media only screen and (min-width: 992px) {
    .navbar-small-buttons .nav-order {
        display: none;
    }
}

.header-navbar .navbar .nav .nav-item.has-c9-icon {
    margin: 0px;
}

.header-navbar .navbar .nav .nav-item.has-c9-icon .nav-link {
    padding: 14px 13px;
    margin: 0px 2px;
}

.has-c9-icon .nav-link-text,
.has-c9-icon .nav-link .nav-highlight {
    display: none;
}

@media only screen and (max-width: 991px) {
    .has-c9-icon .nav-link-text {
        display: inline-block;
        margin-left: 6px;
    }
}

.has-c9-icon .nav-link:before {
    font-family: 'fontawesome';
    font-size: 1.4rem;
    font-weight: 400;
    transition: color .4s ease-in-out;
}

.c9-icon-email .nav-link:before {
    content: '\f003';
}

.c9-icon-spotify .nav-link:before {
    content: '\f1bc';
}

.c9-icon-youtube .nav-link:before {
    content: '\f16a';
}

.c9-icon-facebook .nav-link:before {
    content: '\f09a';
}

.c9-icon-instagram .nav-link:before {
    content: '\f16d';
}

.c9-icon-soundcloud .nav-link:before {
    content: '\f1be';
}


@-webkit-keyframes fadeInFromNone {
    0% {
        display: none;
        opacity: 0;
    }

    1% {
        display: block;
        opacity: 0;
    }

    100% {
        display: block;
        opacity: 1;
    }
}

@keyframes fadeInFromNone {
    0% {
        display: none;
        opacity: 0;
    }

    1% {
        display: block;
        opacity: 0;
    }

    100% {
        display: block;
        opacity: 1;
    }
}

/****************************************************************************************/
/* nav link hover*/
/****************************************************************************************/

.header-navbar .navbar .nav .nav-item .dropdown-item:hover,
.header-navbar .navbar .nav .nav-item .nav-link:hover,
.header-navbar .navbar:not(.navbar-small) .nav-link:hover,
.header-navbar .dropdown-item:hover,
.header-navbar .navbar-toggler:hover,
.header-navbar .nav-link:hover,
.header-navbar .nav>.nav-item>.nav-link:focus,
.header-navbar .nav-search .btn-nav-search:focus,
.header-navbar .nav-search .btn-nav-search:hover,
.header-navbar .nav-woocommerce .nav-link:hover,
.header-navbar .nav-woocommerce .nav-link:focus,
.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover,
.header-navbar .navbar:not(.navbar-small) .nav .nav-item.active .nav-link:hover,
.header-navbar .navbar:not(.navbar-small) .nav .nav-item .nav-link:hover,
.header-navbar .navbar .nav>.nav-item .nav-link:hover,
.header-navbar .navbar.navbar-small .nav .nav-item.active>.nav-link:hover,
.header-navbar .nav-search .btn-nav-search:focus,
.header-navbar .nav-search .btn-nav-search:hover,
.header-navbar .nav-search .btn-nav-search:hover .fa-search,
.header-navbar .navbar-toggler:focus,
.header-navbar .navbar-toggler:hover,
.header-navbar .navbar.navbar-light .nav-link:hover .fa,
.has-c9-icon .nav-link:hover:before,
.has-c9-icon .nav-link:focus:before {
    color: $warning;
}

.header-navbar .navbar .nav .nav-item>.nav-link:focus,
.header-navbar .navbar .nav .nav-item>.nav-link:active {
    color: $light;
}

.header-navbar .navbar.navbar-small .nav .nav-item.active>.nav-link {
    color: $light;
}

/****************************************************************************************/
/* mobile navigation background*/
/****************************************************************************************/
@media only screen and (max-width: 667px) {
    .header-navbar .navbar {
        background-color: $primary;
    }
}

@media only screen and (max-width: 991px) {

    .header-navbar .navbar-collapse,
    .header-navbar .navbar-collapse.show {
        background-color: $primary;
        left: 0;
    }
}

/****************************************************************************************/
/* transparent page header navigation on desktop */
/****************************************************************************************/

@media only screen and (min-width: 768px) {

    #page-wrapper {
        margin-top: -115px;
    }
}

/****************************************************************************************/
/* typography*/
/****************************************************************************************/

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
.c9 h1,
.c9 h2,
.c9 h3,
.c9 h4,
.c9 h5,
.c9 h6,
.editor-styles-wrapper h1,
.editor-styles-wrapper h2,
.editor-styles-wrapper h3,
.editor-styles-wrapper h4,
.editor-styles-wrapper h5,
.editor-styles-wrapper h6,
.c9-h,
.c9-h.h,
.c9-txl,
.display-1,
.display-2,
.display-3,
.display-4,
.display-5,
.display-6,
.editor-styles-wrapper .c9-cta .c9-h p {
    text-transform: uppercase;
    font-weight: $headings-font-weight;
}

.c9-heading strong {
    font-weight: 900;
}

.c9-heading .c9-h,
.c9-heading .c9-sh,
.c9-heading .c9-txl {
    letter-spacing: -.5px;
}

.single-post .c9 h1 {
    text-transform: none;
    font-weight: $headings-font-weight;
    font-size: 2.8rem;
}

p.wp-block-subhead,
.subhead-h,
.c9-sh,
.c9-heading .c9-h .text-muted,
.c9-heading .c9-sh .text-muted,
.c9-heading .c9-txl .text-muted,
.c9 .c9-sh {
    font-weight: 200;
}

.c9 textarea {
    line-height: 1.5;
}

.entry-content p.has-background,
.editor-styles-wrapper p.has-background {
    margin: 0px;
    padding: 2px 4px;
}

.entry-content p.has-background strong,
.editor-styles-wrapper p.has-background strong {
    display: inline-block;
    line-height: 1;
}

.c9-block-post-grid header .c9-block-post-grid-title a {
    font-weight: 700;
}

#fullscreensearch form .btn {
    font-family: $font-family-base;
    padding: 16px 55px;
    font-size: 1.2rem;
    margin-top: 20px;
}

#fullscreensearch input[type=search] {
    border: 1px solid $gray-200;
    box-shadow: 0.5px 0.866px 4px rgba(61, 61, 61, 0.603);
}

.entry-content p a:not(.wp-block-button__link):not(.nav-tabs):not([class|=c9-share]):not(.c9-smooth):not(.nav-link),
.entry-title a {
    color: $link-color;
}

.entry-title a {
    text-decoration: none;
}

.entry-title a:hover {
    text-decoration: underline;
}

.entry-content .c9-grid-has-background .has-text-color a {
    color: $light !important;
}

.entry-content .c9-grid-has-background .has-text-color a:hover {
    color: $primary !important;
}

.c9 .entry-content p>a:not(.wp-block-button__link):hover,
.entry-content p>a:not(.wp-block-button__link):not(.nav-tabs):not([class|=c9-share]):not(.c9-smooth):hover,
.entry-title a:hover {
    color: $link-hover-color;
}

.entry-content .has-covertnine-palette-1-color a {
    color: $light;
}

.entry-content .has-covertnine-palette-1-color a:hover {
    color: $gray-500;
}

.entry-content .c9-cta-content .c9-cta-text p,
.c9-cta-content .c9-cta-text p {
    font-size: inherit;
}

/****************************************************************************************/
/* post navigation, tags, categories, on single posts */
/****************************************************************************************/
.single .navigation .nav-next a,
.single .navigation .nav-previous a {
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 1px;
}

.single .navigation .nav-next a:hover,
.single .navigation .nav-previous a:hover {
    color: $primary;
}

@media only screen and (min-width: 768px) {
    .single .entry-footer {
        padding: 0px;
    }
}

.entry-footer .cat-links {
    font-size: 1.4rem;
}

.entry-footer .cat-links,
.entry-footer .tag-links {
    margin-bottom: 20px;
    display: block;
}

/* tags */
.i-tag,
.tags-links,
.tags-links a {
    font-weight: 900;
    letter-spacing: 1px;
    text-decoration: none;
    text-transform: uppercase;
    color: $gray-900;
    font-size: 1.2rem;
}

.tags-links a {
    padding-left: 8px;
}

.i-tag:hover,
.tags-links a:hover {
    color: $dark;
}

.i-tag:before,
.tags-links a:before {
    display: inline;
    content: "\f02b";
    font: normal normal normal 1.2rem/1 FontAwesome;
    color: $primary;
    padding-right: 2px;
}

/****************************************************************************************/
/* index, posts, blog index */
/****************************************************************************************/

.c9-blog-releases {
    display: -ms-flex;
    display: flex;
    position: relative;
    overflow: visible;
    margin-top: 1.5em;
}

.c9-blog-releases .c9-blog-header {
    flex: 0 0 100%;
    max-width: 100%;
    display: -ms-flex;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media only screen and (min-width: 415px) {
    .c9-blog-releases .c9-blog-header {
        flex: 0 0 50%;
        max-width: 50%;
        min-height: calc(70vh - 108px);
        margin-right: 2em;
        margin-bottom: 0;
    }
}

.c9-blog-info {
    border: 1px solid $gray-200;
    box-shadow: 0.5px 0.866px 4px rgba(61, 61, 61, 0.1);
    background-color: $light;
}

.c9-blog-info h1 {
    font-size: 4rem;
    line-height: .9;
}

.c9-blog-releases .c9-blog-posts {
    background-color: $light;
    overflow-y: scroll;
    max-height: calc(100vh - 108px);
    -ms-scroll-snap-type: y proximity;
    scroll-snap-type: y proximity;
}

.c9-blog-releases .c9-blog-posts .entry-article .entry-image {
    min-width: 100%;
    height: auto;
    background-color: #ccc;
    border: .5px solid $gray-200;
}

.c9-blog-releases .c9-blog-posts .entry-article .post {
    width: 100%;
}

.c9-blog-releases .c9-blog-posts .entry-article {
    display: -ms-flex;
    display: flex;
    align-items: center;
    justify-content: center;
    scroll-snap-align: start;
}

.c9-blog-posts .entry-footer {
    margin-bottom: 30px;
}

@media only screen and (max-width: 414px) {
    .c9-blog-releases {
        display: block;
    }

    .c9-blog-releases .c9-blog-posts {
        overflow-y: visible;
        max-height: none;
    }

    .c9-blog-releases .c9-blog-header {
        min-height: 50vh;
    }

    .c9-blog-posts .entry-footer {
        padding: 0px;
    }
}

.c9-blog-releases .c9-blog-posts figure {
    position: relative;
    overflow: hidden;
}

.c9-blog-releases .c9-blog-posts figure::before {
    position: absolute;
    top: 0;
    left: -75%;
    z-index: 2;
    display: block;
    content: '';
    width: 50%;
    height: 100%;
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, .3) 100%);
    transform: skewX(-25deg);
}

.c9-blog-releases .c9-blog-posts figure:hover::before {
    -webkit-animation: shine .75s;
    animation: shine .75s;
}

@-webkit-keyframes shine {
    100% {
        left: 125%;
    }
}

@keyframes shine {
    100% {
        left: 125%;
    }
}

.c9-block-post-grid header .c9-block-post-grid-title {
    margin-bottom: 0px;
}

.c9-block-post-grid .c9-block-post-grid-excerpt,
.c9-block-post-grid .c9-block-post-grid-byline {
    display: none;
}

.c9-block-post-grid .c9-block-post-grid-text {
    display: grid;
    align-items: center;
    justify-content: center;
}

.entry-meta {
    color: #121212;
}

/****************************************************************************************/
/* blog single */
/****************************************************************************************/
.entry-big-header .entry-title-box .entry-meta,
.entry-big-header .entry-title-box .entry-meta .posted-on,
.entry-big-header .entry-title-box .entry-meta .posted-on a,
.entry-big-header .entry-title-box .entry-meta .byline,
.entry-big-header .entry-title-box .entry-meta .author a {
    font-size: 1.2rem;
    color: $light;
}

.entry-big-header .entry-title-box .entry-meta .posted-on a,
.entry-big-header .entry-title-box .entry-meta .author a {
    text-decoration: underline;
}

.entry-big-header .entry-title-box .entry-meta a:hover {
    color: #4CAAB1 !important;
}

/****************************************************************************************/
/* WordPress Gutenberg Block Specifics + Core Blocks */
/****************************************************************************************/

/* invert link colors on posts grid */
.entry-content .light-color-text .c9-block-post-grid-title a,
.editor-styles-wrapper .light-color-text .c9-block-post-grid-title a {
    color: #fff;
    font-weight: 700;
    text-decoration: none;
}

/* carousel captions */
.carousel-item .carousel-caption h5 {
    font-weight: 700;
}

/* tabs + toggles */
.editor-styles-wrapper .c9-vertical-tabs .nav-pills .nav-link,
.c9-vertical-tabs .nav-pills .nav-link {
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 1px;
}

@media only screen and (max-width: 667px) {

    .nav.nav-tabs .nav-item,
    .entry-content .nav.nav-tabs .nav-item {
        margin: 0px;
    }
}

.c9-vertical-tabs .tab-content {
    padding-top: 20px;
    padding-bottom: 20px;
}

.c9-horizontal-tabs .nav-tabs .nav-link.active,
.is-root-container .c9-horizontal-tabs .nav-tabs .nav-link.active {
    background-color: $light;
}

.c9-horizontal-tabs .nav-tabs a:not(.active):hover {
    background-color: $gray-100;
}

.editor-styles-wrapper .c9-vertical-tabs .nav-pills .nav-link.active,
.c9 .c9-vertical-tabs .nav-pills .nav-link.active {
    background-color: $gray-100;
    color: $link-color;
}

/* list items */
.entry-content ol:not(.wp-block-gallery) li,
.entry-content ul:not(.blocks-gallery-grid):not(.nav-tabs):not(.c9-share-list):not(.wp-block-social-links) li {
    margin: 10px 0px;
}

.editor-styles-wrapper .is-root-container ul[data-type="core/list"] li,
.editor-styles-wrapper .editor-rich-text ul:not(.wp-block-gallery) li,
.editor-styles-wrapper .wp-block-latest-posts li,
.entry-content ul:not(.blocks-gallery-grid):not(.wp-block-social-links) li {
    positon: relative;
}

.editor-styles-wrapper .is-root-container ul[data-type="core/list"] li:before,
.editor-styles-wrapper .editor-rich-text ul:not(.wp-block-gallery) li:before,
.editor-styles-wrapper .wp-block-latest-posts li:before,
.entry-content ul:not(.blocks-gallery-grid):not(.wp-block-social-links) li:before {
    font-size: 1.4rem;
    content: "\f18e";
    font-family: fontawesome;
    position: absolute;
    left: -12px;
}

ul[class=""],
.c9-grid .c9-column-inner>ul[class=""],
.is-root-container>ul.wp-block-list,
.editor-styles-wrapper .c9-grid [data-block] ul[class~=wp-block-list],
html :where(.editor-styles-wrapper) .is-root-container>ul.wp-block-list {
    padding: 0px 0px 0px 45px;
}

.entry-content>ul:not(.blocks-gallery-grid),
.entry-content>ul:not(.blocks-gallery-grid) li,
.editor-styles-wrapper .is-root-container ul[data-type="core/list"],
.entry-content ul:not(.blocks-gallery-grid),
.editor-styles-wrapper ul,
.editor-styles-wrapper ul li,
.entry-content ol:not(.wp-block-gallery),
.entry-content ul:not(.blocks-gallery-grid):not(.nav-tabs):not(.c9-share-list):not(.wp-block-social-links),
.entry-content ul.wp-block-list:not(.blocks-gallery-grid),
.entry-content ul.wp-block-list:not(.blocks-gallery-grid) li,
.entry-content ul:not(.blocks-gallery-grid):not(.wp-block-gallery):not(.wc-block-grid__products):not(.gfield_checkbox):not(.gfield_radio):not(.nav-tabs):not(.c9-share-list):not(.products):not(.gform_fields) {
    list-style: none;
}

ul[class=""]:not(.blocks-gallery-grid):not(.nav-tabs):not(.c9-share-list):not(.wp-block-social-links) li,
ul[class=""]:not(.blocks-gallery-grid):not(.nav-tabs):not(.c9-share-list):not(.wp-block-social-links)>li,
.c9-grid .c9-column-inner>ul[class=""]:not(.blocks-gallery-grid):not(.nav-tabs):not(.c9-share-list):not(.wp-block-social-links)>li,
ul[class=""]:not(.wp-block-gallery):not(.wp-block-social-links) li,
.is-root-container>ul.wp-block-list li,
.is-root-container>.c9-grid ul.wp-block-list li,
html :where(.editor-styles-wrapper) ul.wp-block-list li,
.editor-styles-wrapper .c9-grid ul.wp-block-list[data-block] li {
    list-style-type: none;
}

.c9-grid .c9-column-inner>ol[class=""]>li::before,
.c9-grid .c9-column-inner>ol[class=""]>li>ol>li::before,
ol[class=""]>li::before,
ol[class=""]>li>ol>li::before,
ol[class~="wp-block-list"] li>ol>li::before,
ol[class~="wp-block-list"]>li::before {
    width: 22px;
    margin: 0px 0px 0px -22px;
}

.entry-content ol li:before,
.editor-styles-wrapper ol li:before {
    color: $primary;
}

.entry-content ul .dark-color-text {
    color: $dark;
}

.entry-content ul:not(.blocks-gallery-grid) li:before,
.editor-styles-wrapper ul:not(.wp-block-gallery) li:before,
.widget_archive ul li:before,
.widget_categories ul li:before,
.widget_categories ul li:before,
.widget_pages ul li:before,
.widget_meta ul li:before,
.widget_recent_comments ul li:before,
.widget_recent_entries ul li:before,
.widget_rss ul li:before,
.widget_text ul li:before,
