    .single-product.woocommerce .woocommerce-breadcrumb {
        padding-left: 15px;
        padding-right: 15px;
    }

    .woocommerce.woocommerce-page .woocommerce-error>li {
        color: $gray-900 !important;
        padding: 0px !important;
    }

    .single-product.woocommerce div.product .cart .single_add_to_cart_button.button.alt {
        margin: 0px auto;
        display: block;
    }
}

@media only screen and (max-width: 568px) {

    .woocommerce #content div.product div.summary,
    .woocommerce div.product div.summary,
    .woocommerce-page #content div.product div.summary,
    .woocommerce-page div.product div.summary {
        width: 100%;
        float: none;
    }

    .single-product.woocommerce #content div.product div.images,
    .single-product.woocommerce div.product div.images,
    .single-product.woocommerce-page #content div.product div.images,
    .single-product.woocommerce-page div.product div.images {
        width: 100%;
        float: none;
        margin-right: 0%;
    }
}

/****************************************************************************************/
/*breadcrumbs*/

.woocommerce .site-main .woocommerce-breadcrumb {
    max-width: 1140px;
    margin: 30px auto;
    padding-left: 0px;
    letter-spacing: 0.05px;
    text-align: left;
    color: $dark;
    text-transform: uppercase;
}

.woocommerce .site-main .woocommerce-breadcrumb a {
    color: $dark;
    text-decoration: none;
    transition: color 0.3s;
}

.woocommerce .site-main .woocommerce-breadcrumb a:hover {
    color: #9b9b9b;
    text-decoration: none;
}

/****************************************************************************************/
/* product index from breadcrumbs */

.c9.woocommerce #wrapper-navbar {
    background-color: $dark;
}

.c9 .woocommerce-products-header__title {
    font-family: $headings-font-family;
    font-weight: 700;
    font-size: 7.9rem;
    line-height: 1;
}

.c9.woocommerce-result-count {
    font-family: $font-family-base;
}

.page-search-results nav {
    margin: 0px auto;
    text-align: center;
}

.page-search-results nav .pagination {
    max-width: 1140px;
    margin: 0px auto;
    text-align: center;
    display: inline-block;
}

.page-search-results nav .pagination .page-item {
    display: inline-block;
}

.c9.woocommerce nav.woocommerce-pagination,
.page-search-results nav .pagination {
    margin-bottom: 20px;
}

/****************************************************************************************/
/* Footer */
/****************************************************************************************/
.site :focus,
.site button:focus {
    outline-color: $gray-900;
}

.footer-entirety {
    overflow: visible;
    clear: both;
}

.footer-entirety .textwidget {
    font-size: inherit;
}

.footer-entirety .footer-widget {
    font-size: 1.4rem;
}

.footer-entirety .footer-search input[type="submit"] {
    font-size: 1.2rem;
}

@media only screen and (max-width: 1024px) {

    #footer-full-content .row {
        display: grid;
        grid-auto-flow: row dense;
        grid-column: 1 / 4;
    }

    #footer-full-content .row :nth-child(3) {
        grid-column: span 2;
    }

    #footer-full-content .row .col-md-4 {
        max-width: 100%;
    }
}

.widget_nav_menu .menu-item a,
.widget_recent_entries a {
    color: $gray-900;
    text-decoration: none;
}

.widget_nav_menu .menu-item a:hover,
.widget_recent_entries a:hover {
    color: $gray-700;
    text-decoration: underline;
}

.footer-entirety .widget-title {
    font-size: 1.4rem;
}

.widget ul li,
.widget_archive ul li,
.widget_categories ul li,
.widget_meta ul li,
.widget_nav_menu ul li,
.widget_pages ul li,
.widget_recent_comments ul li,
.widget_recent_entries ul li,
.widget_rss ul li,
.widget_text ul li,
.widget_recent_entries ul li {
    margin: 14px 0px;
}

.footer-search-wrapper .footer-search .input-group>.input-group-append>.btn:hover {
    color: $light;
    background-color: $primary;
}